.content-box {
  margin-bottom: 7px;
  border-radius: 3px;
  background: #E2E2E2;
  border: 1px solid #E2E2E2;
  align-items: center;
  display: flex;
  overflow: hidden;
}
.state {
  background: #E2E2E2;
  color: #161616;
  float: left;
  line-height: 48px;
  width: 8%;
  text-align: center;
  margin: 0 auto;
}
.data {
  color: #9D9D9D;
  background: #FAFAFA;
  float: left;
  width: 92%;
}
.data p {
  margin: 0 !important;
  padding-left: 24px;
  line-height: 48px;
}
.data .p-border {
  border-top: 1px solid #E2E2E2;
}
.clear {
  clear: both;
}
